<template>
    <div class="container">
        <comHeader title="Input"></comHeader>
        <div class="demo">
            <pre>&lt;Input placeholder="" v-model="value">&lt;/Input></pre>
            <p></p>
            <p></p>
            <p>&nbsp;</p>
            <h2>Demo</h2>
            <div class="input">
                <gInput placeholder="" v-model="value"></gInput>
                {{value}}
            </div>
            <div class="input">
                <gInput placeholder="" :clearIcon="true" v-model="value2"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="" :clearIcon="true" value="()"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="disabled" :disabled="true"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="readonly" :readonly="true"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="" :clearIcon="true" :showEye="true" value="" type="password"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="" :clearIcon="true" :showEye="true" type="password"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="()" @focus="_focus"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="()" @blur="_blur"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="input()" @input="_input"></gInput>
            </div>
            <div class="input">
                <gInput placeholder="keyupkeydown()" @keyup="_keyup" @keydown="_keydown"></gInput>
            </div>
            <div class="table">
                <h2>API</h2>
                <table class="table-1">
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                    <tr>
                        <td>class</td>
                        <td></td>
                        <td>inputdiv</td>
                    </tr>
                    <tr>
                        <td>type</td>
                        <td>Stringtext</td>
                        <td>textpassword</td>
                    </tr>
                    <tr>
                        <td>value</td>
                        <td>String</td>
                        <td>v-model</td>
                    </tr>
                    <tr>
                        <td>placeholder</td>
                        <td>String</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>disabled</td>
                        <td>Booleanfalse</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>readonly</td>
                        <td>Booleanfalse</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>maxlength</td>
                        <td>Number50</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>clearIcon</td>
                        <td>Booleanfalse</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>showEye</td>
                        <td>Booleanfalse</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>input</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>blur</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>focus</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>keyup</td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>keydown</td>
                        <td></td>
                        <td></td>
                    </tr>
                </table>
            </div>
        </div>
    </div>
</template>
<script>
    import gInput from '../components/input/index'
    export default {
        name: 'Input',
        path: '/input',
        data(){
            return {
                value: 'v-model',
                value2: '00'
            }
        },
        props: [],
        mounted(){
        },
        components: {gInput},
        methods: {
            _focus(e){
                console.log(e)
            },
            _blur(e){
                console.log(e)
            },
            _input(e){
                console.log(e)
            },
            _keyup(e){
                console.log(e)
            },
            _keydown(e){
                console.log(e)
            }
        },
        computed: {}
    }
</script>
<style>
    .input{ margin-bottom: 10px }
</style>


