<template>
    <div class="container">
        <comHeader title="List"/>
        <div class="demo">
            <list icon="youhuiquan" class="first">
                <div slot="other" class="list-slot">123</div>
            </list>
            <list icon="meg">
                <div slot="other">123</div>
            </list>
            <list icon="xin"></list>
            <list icon="zuji"></list>
            <list icon="server"></list>
            <list icon="tips" :arrow="false"></list>
            <list href="/" icon="loading"></list>
            <h2>API</h2>
            <h3>List</h3>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>icon</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>arrow</td>
                    <td>Boolean|true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>href</td>
                    <td>String</td>
                    <td></td>
                </tr>
            </table>
            <h3>slot</h3>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>other</td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import list from '../components/list/index'
    export default {
        name: 'List',
        path: '/list',
        data(){
            return {}
        },
        props: [],
        mounted(){
        },
        components: {list},
        methods: {},
        computed: {}
    }
</script>
<style>
    .list-item{}
    .list-item:nth-child(1) .list-icon{color: #ff3d66}
    .list-item:nth-child(2) .list-icon{color: #3d98ff}
    .list-item:nth-child(3) .list-icon{color: #ff7d3d}
    .list-item:nth-child(4) .list-icon{color: #13de09}
    .list-item:nth-child(5) .list-icon{color: #ff623d}
</style>


