<!--

touchMovetouchStart
-->
<template>
    <div class="swiper-container" ref="container" @touchstart="_touchStart"
         @touchmove="_touchMove"
         @touchend="_touchEnd"
         @touchcancel="_touchEnd">
        <div class="swiper-wrapper" :style="styleWrap">
            <slot/>
        </div>
        <div class="swiper-pagination" v-if="pagination">
            <span v-for="(item,index) in swipes" :class="{'swiper-pagination-active':index+1==current}"></span>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    export default {
        name: 'swiper',
        data () {
            return {
                containerWidth: '',//
                swipes: [],//itemitem
                translate: 0,//
                duration2: this.duration,//transition-duration
                startX: 0,//
                current: this.active,//1
                timer: ''
            }
        },
        props: {
            active: {//
                type: Number,
                default: 1
            },
            showTime: {//
                type: Number,
                default: 3000
            },
            autoPlay: {
                type: Boolean,
                default: true
            },
            slideAfter: Function,
            distance: {//
                type: Number,
                default: 50
            },
            duration: {//transition-duration
                type: Number,
                default: 500
            },
            pagination: {//
                type: Boolean,
                default: true
            }
        },
        components: {},
        methods: {
            _getTouch(event){
                return event.changedTouches[0] || event.touches[0];
            },
            _touchStart(event){
                clearInterval(this.timer);
                const touch = this._getTouch(event);
                this.startX = touch.clientX;
                //item
                /*for (let i in this.swipes) {
                 this.swipes[i].offset = 0;
                 }
                 //+1
                 const length = this.swipes.length;
                 if (this.current > length) {
                 this.current = 1;
                 } else if (this.current == 0) {
                 //0(translate)current=0
                 this.current = length;
                 }
                 this._translate(0);*/
                event.preventDefault()
            },
            _touchMove(event){
                const touch = this._getTouch(event);
                const distance = touch.clientX - this.startX;
                const startOffset = -this.containerWidth * this.current;//
                const length = this.swipes.length;
                const offset = this.containerWidth * length;//
                //
                if (this.current == length && distance < 0) {
                    this.swipes[0].offset = offset;//padding-right
                }
                //
                if (this.current == 1 && distance > 0) {
                    this.swipes[length - 1].offset = -offset;//padding-left
                }
                this.duration2 = 0;
                this.translate = startOffset + distance;
                event.preventDefault()
            },
            _touchEnd(event){
                const touch = this._getTouch(event);
                const distance = touch.clientX - this.startX;//0
                if (Math.abs(distance) > this.distance) {
                    //
                    if (distance > 0) {
                        //
                        this.current--
                    } else if (distance < 0) {
                        //
                        this.current++
                    }
                }
                this._translate();
                this._slideAfter();
                this._autoPlay();
            },
            _translate(duration = this.duration){
                this.translate = -this.containerWidth * this.current;
                this.duration2 = duration;
                //
                //autoPlaytouchStart
                let length = this.swipes.length;
                let index = 0;
                if (this.current > length || this.current < 1) {
                    //
                    if (this.current > length) {
                        this.current = 1;
                        //this.swipes[0].offset = 0;
                    } else {
                        this.current = length;
                        //this.swipes[length - 1].offset = 0;
                        index = length - 1;
                    }
                    setTimeout(()=> {
                        this.swipes[index].offset = 0;
                        this._translate(0);
                    }, this.duration)
                }
            },
            _autoPlay(){
                if (this.autoPlay) {
                    const length = this.swipes.length;
                    this.timer = setInterval(()=> {
                        if (this.current == length) {
                            this.swipes[0].offset = this.containerWidth * length;
                        }
                        this.current++;
                        this._translate();
                        /*if (this.current > length) {
                         //
                         setTimeout(()=> {
                         this.current = 1;
                         this.swipes[0].offset = 0;
                         this._translate(0);
                         //debugger
                         }, this.duration)
                         }*/
                        this._slideAfter();
                    }, this.showTime);
                }
            },
            _slideAfter(){
                this.slideAfter ? this.slideAfter(this.current) : "";
            }
        },
        computed: {
            styleWrap(){
                return {
                    width: this.containerWidth * this.swipes.length + 'px',
                    padding: `0 ${this.containerWidth}px`,
                    transform: `translate3d(${this.translate}px,0,0)`,
                    transition: `transform ${this.duration2}ms`
                }
            }
        },
        mounted(){
            this.containerWidth = this.$refs.container.offsetWidth;//
            this._translate();
            this._autoPlay();
        },
        filters: {}
    }
</script>


