<template>
    <div class="container">
        <comHeader title="TabBar"></comHeader>
        <div class="demo">
            <h2>Use</h2>
            <pre>import {TabBar, TabItem} from '../components/tabbar/index'</pre>
<pre>&lt;TabBar>
    &lt;TabItem to="/" icon="icon-home">&lt;/TabItem>
    &lt;TabItem to="/tabbar" :icon="['icon-fenlei2','icon-fenlei']" :active="true">&lt;/TabItem>
    &lt;TabItem to="/" icon="icon-set">&lt;/TabItem>
    &lt;TabItem to="/" icon="icon-my">&lt;/TabItem>
    &lt;/TabBar></pre>
            <h2>API</h2>
            <h3>TabBar</h3>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            </table>
            <h3>TabItem</h3>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>to</td>
                    <td>String</td>
                    <td>url</td>
                </tr>
                <tr>
                    <td>icon</td>
                    <td>String|Array</td>
                    <td>iconArray[active]</td>
                </tr>
                <tr>
                    <td>active</td>
                    <td>Boolean|false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>slot</td>
                    <td></td>
                    <td></td>
                </tr>
            </table>
        </div>
        <TabBar>
            <TabItem to="/" icon="icon-home"></TabItem>
            <TabItem to="/tabbar" :icon="['icon-fenlei2','icon-fenlei']" :active="true"></TabItem>
            <TabItem to="/" icon="icon-set"></TabItem>
            <TabItem to="/" icon="icon-my"><Badge>20</Badge></TabItem>
        </TabBar>
    </div>
</template>
<script>
    import {TabBar, TabItem} from '../components/tabbar/index'
    import Badge from '../components/badge/index'
    export default {
        name: 'tabbar',
        path: '/tabbar',
        data () {
            return {}
        },
        props: {},
        components: {TabBar, TabItem, Badge},
        methods: {},
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


