<template>
    <div class="container">
        <comHeader title="cityPicker"/>
        <div class="demo">
            <h2>Use</h2>
            <pre>import CityPicker from '../components/cityPicker/index'</pre>
            <pre>&lt;CityPicker placeholder="" v-model="value">&lt;/CityPicker></pre>
            <p>picker</p>
            <h2>Demo</h2>
            <CityPicker v-model="value1" placeholder=""></CityPicker><br>
            <CityPicker v-model="value2" placeholder="" :children="2"></CityPicker><br>
            <CityPicker placeholder="" v-model="value3" cancelText="cancel"></CityPicker>
            <h2>API</h2>
            <h3>cityPicker</h3>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>value</td>
                    <td>Array</td>
                    <td></td>
                </tr>
                <tr>
                    <td>placeholder</td>
                    <td>String</td>
                    <td>valueinput</td>
                </tr>
                <tr>
                    <td>children</td>
                    <td>Number3</td>
                    <td>3</td>
                </tr>
                <tr>
                    <td>cancelText</td>
                    <td></td>
                    <td>picker</td>
                </tr>
                <tr>
                    <td>cancelEvent</td>
                    <td></td>
                    <td>picker</td>
                </tr>
                <tr>
                    <td>confirmText</td>
                    <td></td>
                    <td>picker</td>
                </tr>
                <tr>
                    <td>confirmEvent</td>
                    <td></td>
                    <td>picker</td>
                </tr>
                <tr>
                    <td>change</td>
                    <td></td>
                    <td>picker</td>
                </tr>
                <tr>
                    <td>title</td>
                    <td></td>
                    <td>picker</td>
                </tr>
                <tr>
                    <td>visibleCount</td>
                    <td></td>
                    <td>picker</td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import CityPicker from '../components/cityPicker/index'
    export default {
        name: 'cityPicker',
        path: '/cityPicker',
        data () {
            return {
                value1: [],
                value2: [],
                value3: ['', '', '']
            }
        },
        props: {},
        components: {CityPicker},
        methods: {

        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


