<template>
    <div class="container">
        <comHeader title="Badge"></comHeader>
        <div class="demo">
            <h2>Use</h2>
            <pre>&lt;Badge>123&lt;/Badge></pre>
            <List>
                <Badge :isDot="true"></Badge>
            </List>
            <List>
                <Badge>10</Badge>
            </List>
            <List>
                <Badge>200</Badge>
            </List>
            <List>
                <Badge>New</Badge>
            </List>
            <div class="badge-demo">
                <p></p>
                <ul>
                    <li class="icon-daifukuan1">
                        <Badge>108</Badge>
                    </li>
                    <li class="icon-daipingjia">
                        <Badge>10</Badge>
                    </li>
                    <li class="icon-daifahuo">
                        <Badge>0</Badge>
                    </li>
                    <li class="icon-daishouhuo">
                        <Badge>8</Badge>
                    </li>
                </ul>
            </div>
            <h2>API</h2>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>class</td>
                    <td></td>
                    <td></td>
                </tr>
                <tr>
                    <td>isDot</td>
                    <td>Boolean|false</td>
                    <td>badge-dot</td>
                </tr>
                <tr>
                    <td>max</td>
                    <td>Number|99</td>
                    <td>+</td>
                </tr>
                <!--<tr>
                    <td>isTop</td>
                    <td>Boolean|false</td>
                    <td>badge-top</td>
                </tr>-->
                <tr>
                    <td>slot</td>
                    <td></td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import List from '../components/list/index'
    import Badge from '../components/badge/index'
    export default {
        name: 'badge',
        path: '/badge',
        data () {
            return {}
        },
        props: {},
        components: {Badge, List},
        methods: {},
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>
<style>
    .badge-demo{ padding: 20px 10px; }
    .badge-demo ul{ display: flex; padding-top: 20px; }
    .badge-demo li{ width: 100%; text-align: center; font-size: 16px; position: relative; }
    .badge-demo li:before{ font-size: 50px; margin: 0 auto; display: block; color: #795da3; }
    .badge-demo li span{ position: absolute; right: 0; top: 0 }
</style>


