<template>
    <div class="container">
        <comHeader title="pullRefreshLoading"/>
        <pull :onLoading="_onLoading" :onRefresh="_onRefresh">
            <div class="demo">
                <h2>API</h2>
                <table class="table-1">
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                    <tr>
                        <td>duration</td>
                        <td>Number|500</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>distance</td>
                        <td>Number|50</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>refreshTips</td>
                        <td>Array</td>
                        <td>['', '', '']</td>
                    </tr>
                    <tr>
                        <td>loadingTips</td>
                        <td>Array</td>
                        <td>['', '', '', '']</td>
                    </tr>
                    <tr>
                        <td>onRefresh</td>
                        <td>Function</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>onLoading</td>
                        <td>Function</td>
                        <td></td>
                    </tr>
                </table>
                <pre>onLoading(fn){
            //
            fn();//fn(0)
            }</pre>
            </div>
            <List v-for="(item,index) in data" :key="index" v-text="item"></List>
        </pull>
    </div>
</template>
<script type="text/ecmascript-6">
    import List from '../components/list/index'
    import Pull from '../components/pull/index'
    export default {
        name: 'pullRefreshLoading',
        path: '/pull',
        data () {
            return {
                data: [1, 2, 3, 4, 5, 6, 7, 8],
                page: 1
            }
        },
        props: {},
        components: {Pull, List},
        methods: {
            _onLoading(refresh){
                console.log('_onLoading');
                let page;
                setTimeout(()=> {
                    this.page++;
                    //3
                    if (this.page == 3) {
                        page = 0
                    }
                    for (let i = 0; i < 10; i++) {
                        this.data.push('' + this.page + '' + i);
                    }
                    //
                    refresh(page);
                }, 5000)
            },
            _onRefresh(refresh){
                console.log('_onRefresh');
                //
                setTimeout(function () {
                    window.location.reload();
                    refresh();
                }, 5000)
            }
        },
        computed: {},
        mounted(){

        },
        filters: {}
    }
</script>


