/*pages.js*/
let array = [];
for (let i in pages) {
    let page = pages[i].default;
    array.push({
        path: page.path,
        name: page.name,
        component: page
    });
}
export default new Router({
    routes: array,
   /* mode: 'history',//history scrollBehavior hashbangwindow.scrollTo(0,0)
    scrollBehavior (to, from, savedPosition) {
        return {x: 0, y: 0}
    }*/
})
