<template>
    <div class="container">
        <comHeader title="Keyboard"/>
        <div class="demo">
            <h2>Use</h2>
            <p></p>
            <br>
            <Keyboard v-model="value1"></Keyboard>
            <br>
            <Keyboard placeholder="placeholder" v-model="value2"></Keyboard>
            <br>
            <Keyboard placeholder="password" :password="true" v-model="value3"></Keyboard>
            <br>
            <Keyboard placeholder="maxLength=3" v-model="value4" :maxLength="3"></Keyboard>
            <br>
            <Keyboard placeholder="" v-model="value5" :float="false"></Keyboard>
            <br>
            <Keyboard placeholder="confirmText" v-model="value6" confirmText="ok"></Keyboard>
            <br>
            <Keyboard placeholder="" v-model="value7"></Keyboard>

            <h2>API</h2>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>value</td>
                    <td></td>
                    <td>v-model</td>
                </tr>
                <tr>
                    <td>confirmText</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>confirmEvent</td>
                    <td>Function</td>
                    <td></td>
                </tr>
                <tr>
                    <td>float</td>
                    <td>Boolean|true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>placeholder</td>
                    <td>String</td>
                    <td>input</td>
                </tr>
                <tr>
                    <td>password</td>
                    <td>Boolean|false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>maxLength</td>
                    <td>Number</td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import Keyboard from '../components/keyboard/index'
    export default {
        name: 'keyboard',
        path: '/keyboard',
        data(){
            return {
                value1: '',
                value2: '',
                value3: '',
                value4: '',
                value5: '',
                value6: '',
                value7: '',
            }
        },
        props: [],
        mounted(){
        },
        components: {Keyboard},
        methods: {},
        computed: {}
    }
</script>


