<template>
    <div class="container">
        <comHeader title="Tabs"></comHeader>
        <div class="demo">
            <Tabs value="t2">
                <TabPane label="tabs 1" name="t1">this is tabs 1 content</TabPane>
                <TabPane label="tabs 2" name="t2"></TabPane>
                <TabPane label="tabs 3" name="t3">this is tabs 3 content</TabPane>
            </Tabs>
            <br><br>
            <Tabs value="t1" animation="tabs-slide">
                <TabPane label="tabs 1" name="t1"></TabPane>
                <TabPane label="tabs 2" name="t2">this is tabs 2 content</TabPane>
                <TabPane label="tabs 3" name="t3">this is tabs 3 content</TabPane>
            </Tabs>
            <br><br>
            <Tabs value="t1" animation="tabs-slide" class="tabs-animation">
                <TabPane label="tabs 1" name="t1">active</TabPane>
                <TabPane label="tabs 2" name="t2">this is tabs 2 content</TabPane>
                <TabPane label="tabs 3" name="t3">this is tabs 3 content</TabPane>
            </Tabs>
            <br><br>
            <h2>API</h2>
            <h3>Tabs</h3>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>value</td>
                    <td> tab  name v-model </td>
                </tr>
                <tr>
                    <td>class</td>
                    <td>Tabs</td>
                </tr>
                <tr>
                    <td>animation</td>
                    <td>tabs-fadetabs-slide****-enter-active</td>
                </tr>
                <tr>
                    <td>onChange</td>
                    <td></td>
                </tr>
            </table>
            <h3>TabPane</h3>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>class</td>
                    <td>TabPane</td>
                </tr>
                <tr>
                    <td>label</td>
                    <td></td>
                </tr>
                <tr>
                    <td>disabled</td>
                    <td></td>
                </tr>
                <tr>
                    <td>name</td>
                    <td>Tabsvalue</td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import {Tabs, TabPane} from '../components/tabs/index'
    export default {
        name: 'tab',
        path: '/tab',
        data () {
            return {}
        },
        props: {},
        components: {Tabs, TabPane},
        methods: {},
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>
<style>
    .tabs-animation{ }
    .tabs-animation .tabs-tab{ position: relative }
    .tabs-animation .tabs-tab:after{ content: ''; display: block; height: 1px; background: #04ab02;margin: 0 auto;width: 0;transition: all .5s }
    .tabs-animation .tabs-tab.active:after{width: 100%}
</style>


