<template>
    <div class="container">
        <comHeader title="Swiper"/>
        <div class="demo">
            <h2>Demo</h2>
            <Swiper>
                <SwiperItem>01</SwiperItem>
                <SwiperItem>02</SwiperItem>
                <SwiperItem>03</SwiperItem>
                <SwiperItem>04</SwiperItem>
            </Swiper>
            <br>
            <Swiper :autoPlay="false" :slideAfter="_slideAfter">
                <SwiperItem>01 autoPlay="false" slideAfter="_slideAfter"</SwiperItem>
                <SwiperItem>02 autoPlay="false" slideAfter="_slideAfter"</SwiperItem>
                <SwiperItem>03 autoPlay="false" slideAfter="_slideAfter"</SwiperItem>
                <SwiperItem>04 autoPlay="false" slideAfter="_slideAfter"</SwiperItem>
            </Swiper>
            <br>
            <Swiper :pagination="false" :active="2">
                <SwiperItem></SwiperItem>
                <SwiperItem>02</SwiperItem>
                <SwiperItem>03</SwiperItem>
                <SwiperItem>04</SwiperItem>
            </Swiper>
            <br>
            <h2>API</h2>
            <h3>Swiper</h3>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>active</td>
                    <td>Number|1</td>
                    <td></td>
                </tr>
                <tr>
                    <td>showTime</td>
                    <td>Number|3000</td>
                    <td></td>
                </tr>
                <tr>
                    <td>autoPlay</td>
                    <td>Boolean|true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>slideAfter</td>
                    <td>Function</td>
                    <td></td>
                </tr>
                <tr>
                    <td>distance</td>
                    <td>Number|50</td>
                    <td>px</td>
                </tr>
                <tr>
                    <td>duration</td>
                    <td>Number|500</td>
                    <td>transition-duration</td>
                </tr>
                <tr>
                    <td>pagination</td>
                    <td>Boolean|true</td>
                    <td></td>
                </tr>
            </table>
            <h3>SwiperItem</h3>
            <p></p>
        </div>
    </div>
    </div>
</template>
<script>
    import {Swiper, SwiperItem} from '../components/swiper/index'
    export default {
        name: 'swiper',
        path: '/swiper',
        data () {
            return {}
        },
        props: {},
        components: {Swiper, SwiperItem},
        methods: {
            _slideAfter(index){
                console.log(index);
            }
        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


