<template>
    <div>
        <v-scroll :on-refresh="onRefresh" :on-infinite="onInfinite">
            <ul>
                <li v-for="(item,index) in listdata" >{{item.name}}</li>
                <li v-for="(item,index) in downdata" >{{item.name}}</li>
            </ul>
        </v-scroll>
    </div>
</template>
<script>
    import Scroll from '../components/test/test';

    export default{
        path:'/test',
        data () {
            return {
                counter : 1, //15 count16
                num : 15,  // 
                pageStart : 0, // 
                pageEnd : 0, // 
                listdata: [], // 
                downdata: []  // 
            }
        },
        mounted : function(){
            this.getList();
        },
        methods: {
            getList(){
                let vm = this;
                vm.$http.get('https://api.github.com/repos/typecho-fans/plugins/contents/').then((response) => {
                    vm.listdata = response.data.slice(0,15);
            }, (response) => {
                    console.log('error');
                });
            },
            onRefresh(done) {
                this.getList();

                done() // call done

            },
            onInfinite(done) {
                let vm = this;
                vm.$http.get('https://api.github.com/repos/typecho-fans/plugins/contents/').then((response) => {
                    vm.counter++;
                vm.pageEnd = vm.num * vm.counter;
                vm.pageStart = vm.pageEnd - vm.num;
                let arr = response.data;
                let i = vm.pageStart;
                let end = vm.pageEnd;
                for(; i<end; i++){
                    let obj ={};
                    obj["name"] = arr[i].name;
                    vm.downdata.push(obj);
                    if((i + 1) >= response.data.length){
                        this.$el.querySelector('.load-more').style.display = 'none';
                        return;
                    }
                }
                done() // call done
            }, (response) => {
                    console.log('error');
                });
            }
        },
        components : {
            'v-scroll': Scroll
        }
    }
</script>


