<template>
    <div class="dialog" v-if="!insertDialog" :class="{[className]:className}">
        <transition name="fade">
            <div class="overlay" v-if="maskLayer" v-show="visible2" @click="_maskClick"></div>
        </transition>
        <transition :name="transition">
            <div class="dialog-flex" v-show="visible2" @click="_maskClick">
                <div class="dialog-body">
                    <a href="javascript:;" class="dialog-close icon-close" @click="_closeClick" v-if="showClose"></a>
                    <div class="dialog-title" v-text="title" v-if="title"></div>
                    <div class="dialog-content">
                        <div class="dialog-text" v-text="content" v-if="content"></div>
                        <slot v-else></slot>
                    </div>
                    <div class="dialog-footer" v-if="confirmText||cancelText">
                        <Btn class="btn-cancel" v-text="cancelText" @click="_cancelClick" v-if="cancelText"></Btn>
                        <Btn class="btn-confirm" v-text="confirmText" @click="_confirmClick" v-if="confirmText"></Btn>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script type="text/ecmascript-6">
    import Btn from '../button/index'
    export default {
        name: 'Dialog',
        data () {
            return {
                insertDialog: false,//this.$dialog
                visible2: this.visible,
                clearTime: '',//
                autoTime: ''//
            }
        },
        props: {
            className: String,
            maskLayer: {//
                type: Boolean,
                default: true
            },
            maskClose: {//
                type: Boolean,
                default: true
            },
            visible: {//
                type: Boolean,
                default: false
            },
            insert: {//body
                type: Boolean,
                default: false
            },
            transition: {//
                type: String,
                default: 'scale'
            },
            autoClose: {
                type: Number,
                default: 0
            },
            showClose: {//
                type: Boolean,
                default: false
            },
            closeEvent: Function,
            title: String,//dialog
            content: String,
            confirmText: String,//
            confirmEvent: Function,
            cancelText: String,//
            cancelEvent: Function
        },
        watch: {
            visible(v){
                if (v) {
                    this.open();
                }
            }
        },
        components: {Btn},
        methods: {
            _maskClick(e){
                //dialog-flexdialog-flex
                //
                this.maskLayer && this.maskClose ? this._closeClick(e) : "";
            },
            _close(){
                //
                this.$emit('update:visible', false);
                this.visible2 = false;
                if (this.insert) {
                    //body
                    // 
                    setTimeout(()=> {
                        this.insertDialog = true;
                    }, 500);
                }
                clearInterval(this.clearTime);
            },
            _closeClick(e){
                this.closeEvent ? this.closeEvent(e) : this._close();
                e.stopPropagation();
            },
            open(){
                //this.$dialog
                this.visible2 = true;
                this._autoClose();
            },
            close(){
                //open
                this._close();
            },
            _cancelClick(e){
                this.cancelEvent ? this.cancelEvent(e) : this._close();
                e.stopPropagation();
            },
            _confirmClick(e){
                this.confirmEvent ? this.confirmEvent(e) : this._close();
                e.stopPropagation();
            },
            _autoClose(){
                //
                if (this.autoClose > 0) {
                    this.autoTime = this.autoClose;
                    this.clearTime = setInterval(()=> {
                        if (this.autoTime > 0) {
                            this.autoTime--;
                        } else {
                            this._close();
                        }
                    }, 1000);
                }
            }
        },
        computed: {},
        mounted(){

        },
        filters: {}
    }
</script>


