<template>
    <div class="container">
        <vHeader title=""></vHeader>
        <br>
        <vHeader title="">
            <div slot="right" class="right"></div>
        </vHeader>
        <br>
        <vHeader title="" :back="false"></vHeader>
        <br>
        <vHeader title="url" backUrl="/"></vHeader>

        <div class="demo">
            <h2>API</h2>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>class</td>
                    <td>null</td>
                    <td></td>
                </tr>
                <tr>
                    <td>title</td>
                    <td>null</td>
                    <td></td>
                </tr>
                <tr>
                    <td>back</td>
                    <td>Boolean|true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>backUrl</td>
                    <td>String|null</td>
                    <td></td>
                </tr>
                <tr>
                    <td>slot</td>
                    <td>right</td>
                    <td>right</td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import vHeader from '../components/header/index'
    export default {
        name: 'Header',
        path: '/header',
        data () {
            return {}
        },
        props: {},
        components: {vHeader},
        methods: {},
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


