<template>
    <div class="container">
        <comHeader :title="msg" :back="false"/>
        <List v-for="(item,index) in link" :href="item.link" :key="index">{{item.title}}</List>
        <!--<div class="hello">
            <h1>{{ msg }}</h1>
            <ul>
                <li v-for="item in link">
                    <router-link :to="item.link">{{item.title}}</router-link>
                </li>
            </ul>
        </div>-->
    </div>

</template>
<script>
    import List from '../components/list/index'
    export default {
        name: 'index',
        path: '/',
        data () {
            return {
                msg: 'Vue.js@2.5 components for mobile',
                link: [
                    {
                        title: 'Badge ',
                        link: '/badge'
                    },
                    {
                        title: 'Button ',
                        link: '/button'
                    },
                    {
                        title: 'Checkbox ',
                        link: '/checkbox'
                    },
                    {
                        title: 'CityPicker ',
                        link: '/cityPicker'
                    },
                    {
                        title: 'DatePicker ',
                        link: '/datePicker'
                    },
                    {
                        title: 'Dialog ',
                        link: '/dialog'
                    },
                    {
                        title: 'form  X',
                        link: '/form'
                    },
                    {
                        title: 'formItem  X',
                        link: '/formItem'
                    },
                    {
                        title: 'Header ',
                        link: '/header'
                    },
                    {
                        title: 'Input ',
                        link: '/input'
                    },
                    {
                        title: 'InputNumber ',
                        link: '/inputNumber'
                    },
                    {
