                        link:'/keyboard'
                    },
                    {
                        title: 'List ',
                        link: '/list'
                    },
                    {
                        title: 'Loading ',
                        link: '/loading'
                    },
                    {
                        title: 'Picker ',
                        link: '/picker'
                    },
                    {
                        title: 'Progress ',
                        link: '/progress'
                    },
                    {
                        title: 'Pull ',
                        link: '/pull'
                    },
                    {
                        title: 'Radio ',
                        link: '/radio'
                    },
                    {
                        title: 'Select ',
                        link: '/select'
                    },
                    {
                        title: 'Swiper ',
                        link: '/swiper'
                    },
                    {
                        title: 'Switch ',
                        link: '/switch'
                    },
                    {
                        title: 'tab ',
                        link: '/tab'
                    },
                    {
                        title: 'tabbar ',
                        link: '/tabbar'
                    },
                    {
                        title: 'Textarea ',
                        link: '/textarea'
                    },
                    {
                        title: 'Upload ',
                        link: '/upload'
                    }
                ]
            }
        },
        props: {},
        components: {List},
        methods: {},
        computed: {},
        mounted(){
            console.log(this.link.length);
        },
        filters: {}
    }
</script>


