<template>
    <div class="container">
        <comHeader title="formItem"></comHeader>
        <div class="demo">
            <FormItem label="" labelClass="icon-user">
                <vInput></vInput>
            </FormItem>
            <FormItem label="" labelClass="icon-lock1">
                <vInput placeholder="" type="password"></vInput>
            </FormItem>
            <FormItem labelClass="icon-user">
                <vInput></vInput>
            </FormItem>
            <FormItem label="radio">
                <Radio></Radio>
            </FormItem>
            <FormItem label="RadioGroup">
                <RadioGroup :data="radioGroup"></RadioGroup>
            </FormItem>
            <FormItem label="Checkbox">
                <Checkbox></Checkbox>
            </FormItem>
            <FormItem label="Checkbox">
                <CheckboxGroup :data="groupDate" v-model="group"></CheckboxGroup>
            </FormItem>
            <FormItem label="">
                <CityPicker v-model="value1" placeholder=""></CityPicker>
            </FormItem>
            <FormItem label="">
                <DatePicker placeholder="" v-model="datePicker"></DatePicker>
            </FormItem>
            <h2>API</h2>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>label</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>labelClass</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>slot</td>
                    <td></td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import FormItem from '../components/formItem/index'
    import vInput from '../components/input/index'
    import {Radio, RadioGroup} from '../components/radio/index'
    import {Checkbox, CheckboxGroup} from '../components/checkbox/index'
    import CityPicker from '../components/cityPicker/index'
    import DatePicker from '../components/datePicker/index'
    export default {
        name: 'formItem',
        path: '/formItem',
        data () {
            return {
                radioGroup: [
                    {text: '', value: 'a1'},
                    {text: '', value: 'a2'}
                ],
                groupDate: [
                    {text: '1', value: 'a1'},
                    {text: '2', value: 'a2'},
                    {text: '3', value: 'a3'}
                ],
                group: ['a1'],
                value1: ['', '', ''],
                datePicker: ''
            }
        },
        props: {},
        components: {FormItem, vInput, Radio, RadioGroup, Checkbox, CheckboxGroup, CityPicker, DatePicker},
        methods: {},
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


