<!--Created by 337547038 on 2018/4/12.-->
<template>
    <div class="container">
        <comHeader title="DatePicker"/>
        <div class="demo">
            <p>pickerdatePicker</p>
            <h2>Demo</h2>
            <DatePicker v-model="value0" placeholder=""></DatePicker>
            <br>
            <DatePicker v-model="value1" placeholder=""></DatePicker>
            <br>
            <DatePicker v-model="value2" placeholder="" type="y"></DatePicker>
            <br>
            <DatePicker v-model="value3" placeholder="" type="ym"></DatePicker>
            <br>
            <DatePicker v-model="value4" placeholder="" type="ymdhms"></DatePicker>
            <br>
            <DatePicker v-model="value5" placeholder="()" type="ymdhms" :showUnit="true"></DatePicker>
            <br>
            <DatePicker v-model="value5" placeholder="(100)" type="ymdhms" :fill0="true"></DatePicker>
            <br>
            <DatePicker v-model="value6" placeholder="2015/5/1-2020/10/1" min="2015/5/1"
                        max="2020/10/1"></DatePicker>
            <br>
            <h2>API</h2>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>value</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>min</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>max</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>showUnit</td>
                    <td>Boolean|false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>fill0</td>
                    <td>Boolean|false</td>
                    <td>100</td>
                </tr>
                <tr>
                    <td>type</td>
                    <td>String|ymd</td>
                    <td>yymymd()ymdhymdhmymdhms</td>
                </tr>
                <tr>
                    <td>picker</td>
                    <td></td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import DatePicker from '../components/datePicker/index'
    export default {
        name: 'datePicker',
        path: '/datePicker',
        data () {
            return {
                value0: '2018-4-12',
                value1: '',
                value2: '',
                value3: '',
                value4: '',
                value5: '',
                value6: ''
            }
        },
        props: {},
        components: {DatePicker},
        methods: {},
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>


