<template>
    <div class="container">
        <comHeader title="Dialog"></comHeader>
        <div class="demo">
            <h2>Use</h2>
            <pre>this.$dialog({content:'this is content'})</pre>
            <p>dialogcss</p>
            <List @click="_openDialog(1)">1.</List>
            <List @click="_openDialog(2)">2.</List>
            <List @click="_openDialog(3)">3.</List>
            <List @click="_openDialog(4)">4.</List>
            <List @click="_openDialog(5)">5.</List>
            <List @click="_openDialog(6)">6.</List>
            <List @click="_openDialog(7)">7.</List>
            <h2>Use</h2>
            <p></p>
            <pre>import Dialog from '../components/dialog/dialog'</pre>
            <pre>&lt;Dialog :visible.sync="visible">this is content&lt;/Dialog></pre>
            <List @click="visible=true">8.</List>
            <vDialog :visible.sync="visible">this is content</vDialog>
            <h2>API</h2>
            <table class="table-1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr>
                    <td>className</td>
                    <td>String</td>
                    <td>class</td>
                </tr>
                <tr>
                    <td>maskLayer</td>
                    <td>Boolean|true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>maskClose</td>
                    <td>Boolean|true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>visible</td>
                    <td>Boolean|false</td>
                    <td>syncthis.$dialog({})</td>
                </tr>
                <tr>
                    <td>transition</td>
                    <td>String|scale</td>
                    <td>cssdialog</td>
                </tr>
                <tr>
                    <td>autoClose</td>
                    <td>Number|0</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>showClose</td>
                    <td>Boolean|false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>closeEvent</td>
                    <td>Function</td>
                    <td></td>
                </tr>
                <tr>
                    <td>title</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>content</td>
                    <td>String</td>
                    <td>slot</td>
                </tr>
                <tr>
                    <td>confirmText</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>confirmEvent</td>
                    <td>Function</td>
                    <td></td>
                </tr>
                <tr>
                    <td>cancelText</td>
                    <td>String</td>
                    <td></td>
                </tr>
                <tr>
                    <td>cancelText</td>
                    <td>Function</td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
    import List from '../components/list/index'
    import vDialog from '../components/dialog/dialog'
    export default {
        name: 'DialogPage',
        path: '/dialog',
        data () {
            return {
                visible: false
            }
        },
        props: {},
        components: {vDialog, List},
        methods: {
            _openDialog(index){
                if (index == 1) {
                    this.$dialog({
                        content: 'this is content',
                        visible: true
                    });
                } else if (index == 2) {
                    this.$dialog({
                        title: 'title',
                        content: 'this is content',
                        cancelText: 'cancel',
                        confirmText: 'confirm'
                    });
                } else if (index == 3) {
                    this.$dialog({
                        title: 'title',
                        content: 'this is content',
                        cancelText: 'cancel',
                        confirmText: 'confirm',
                        cancelEvent() {
                            alert('');
                            this.close();
                        },
                        confirmEvent() {
                            alert('');
                            this.close();
                        }
                    });
                } else if (index == 4) {
                    this.$dialog({
                        title: 'title',
                        content: 'this is content',
                        showClose: true,
                        confirmText: 'confirm'
                    });
                } else if (index == 5) {
                    this.$dialog({
                        title: 'title',
                        content: 'this is content',
                        autoClose: 3,
                        confirmText: 'confirm'
                    });
                } else if (index == 6) {
                    this.$dialog({
                        title: 'title',
                        content: 'this is content',
                        transition: 'shake',
                        confirmText: 'confirm'
                    });
                }
                else if (index == 7) {
                    this.$dialog({
                        className: 'dialog-test',
                        content: 'dialog'
                    });
                }
            }
        },
        computed: {},
        mounted(){
        },
        filters: {}
    }
</script>
<style>
    .dialog-test .dialog-flex{ display: block; bottom: auto }
    .dialog-test .dialog-flex .dialog-body{ width: 100% }
</style>


